export function splitFileIntoChunk(file) {
    const { uploadUrls, chunkSize, raw } = file;
    const fileSize = raw.size;
    return uploadUrls.map(({ partNum, uploadUrl }) => {
        const start = partNum * chunkSize;
        const end = Math.min((partNum + 1) * chunkSize, fileSize);
        const chunk = raw.slice(start, end);
        return {
            url: { partNum, uploadUrl },
            chunk,
        };
    });
}
