import { GB, MB, KB } from './constant';
export function calcDisplaySize(size) {
    const units = [
        { value: GB, label: 'GB' },
        { value: MB, label: 'MB' },
        { value: KB, label: 'KB' },
        { value: 1, label: 'B' },
    ];
    for (const { value, label } of units) {
        if (size >= value || value === 1) {
            return (size / value).toFixed(1) + label;
        }
    }
    // ,,
    return size + 'B';
}
