import { UPLOADS_DATABASE } from '.';
// IndexedDB
export function handleAddFilesToDB(storeName, files) {
    try {
        const transaction = createTransaction(storeName);
        const objectStore = transaction.objectStore(storeName);
        addFilesToObjectStore(objectStore, files);
    }
    catch (error) {
        console.warn(`: ${error.message}`);
    }
}
// 
function createTransaction(storeName) {
    const transaction = UPLOADS_DATABASE.transaction(storeName, 'readwrite');
    transaction.onerror = () => console.warn(`: ${transaction.error}`);
    transaction.onabort = () => console.warn('');
    transaction.oncomplete = () => console.warn('');
    return transaction;
}
// 
function addFilesToObjectStore(objectStore, files) {
    files.map((file) => addSingleFile(objectStore, file));
}
// 
function addSingleFile(objectStore, file) {
    try {
        objectStore.put(file, file.fileName);
        console.warn(` "${file.fileName}" `);
    }
    catch (error) {
        console.warn(` "${file.fileName}" : ${error.message}`);
    }
}
