import { UPLOADS_DATABASE } from '.';
export function deleteFileInStore(storeName, fileName) {
    let transaction;
    try {
        transaction = UPLOADS_DATABASE.transaction(storeName, 'readwrite');
    }
    catch (ex) {
        console.warn('db.transaction() exception in deleteFileInStore() - ' + ex.message);
        return;
    }
    try {
        const objectStore = transaction.objectStore(storeName);
        const deleteReq = objectStore.delete(fileName);
        deleteReq.onsuccess = () => {
            return true;
        };
        deleteReq.onerror = (_e) => {
            console.warn('deleteReq.onerror fired in deleteFileInStore() - error code: ' + deleteReq.error);
        };
    }
    catch (ex) {
        console.warn('Transaction and/or put() exception in deleteFileInStore() - ' + ex.message);
        return;
    }
}
